import React, {Component} from 'react'
import axios from 'axios'

class Receipt extends React.Component {
    state = {
        transaction: []
    }

    componentDidMount () {
        axios.get(`https://rest.bitcoin.com/v1/rawtransactions/getRawTransaction/{{this.props.txid}}?verbose=true`)
        .then(response => this.setState({transaction: response}))
        .catch(error => {
          this.setState({transaction: []});
          console.error(error);
        });

    }

    render () {
        return (
            <div>
            <div>receipt detail will go here</div>
            <div>{this.props.txid}</div>
            <div>{this.state.transaction}</div>
            </div>
        );
    }
}

export default Receipt;



